<template>
    <div class="overlay-content" style="border: solid">
        <!-- <p @click="click('닫기')">닫기</p> -->
        <label>DirectHandler 이름을 영문 한단어로 입력하세요</label>
        <input v-model="name" type = "text" placeholder="ex> myresponse">
        <label for="cars">API 선택</label>
        <select v-model="api">
            <option v-for="(item, index) in data.apis" v-bind:key="index" >{{item}}</option>
        </select>
        <br>
        <input type="button" style="width:100%" @click="click('등록')" value="등록">
        <p style="color: red;">{{info}}</p>
    </div>
</template>
<style scoped>
input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
</style>

<script>
import responseType from '../project/responseType';
import builderType from '../dictionary/builderType';
// import intentType from '../intentType';
import common from '../utils/common';

export default {
    name: 'DirectRegi', //SKelton:DefineComponentName
    props: {
        data: Object,
    },
    data: function(){
        return {
            name: '',
            api: '',
            component: '',
            info: '',
        }
    },
    mounted() {
        console.log('ResponseRegi.mounted', this.data);
    },    
    computed: {
    },
    filters: {
    },
    methods: {
        click(type){
            switch (type) {
                case '등록':
                    {
                        const name = this.name.trim();
                        if(name === ''){
                            this.info = 'Response 이름을 입력하세요.';
                            return;
                        }
                        console.log('response>', this.api);
                        const data = {
                            action: builderType.DIRECT,
                            name, 
                            api: this.api, 
                        }                  
                        console.log('data', data);
                        common.callResponse(responseType.MGBUILDER, data);
                    }
                    break;          
            }
        },
    },
};
</script>